const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			userInfo: {},
			imgUrl: '',
			imageUrl: ''
		}
	},
	onLoad() {
		this.imgUrl = helper.imgUrl
	},
	onShow() {
		this.getUserInfo()
	},
	methods: {
		// 进入页面
		goPageNavigateTo(e) {
			console.log(e)
			let url = e.currentTarget.dataset.name
			if(url == 'meNewbieRead'){
				uni.navigateTo({
					url: `/pages/articleDetail?id=${21}`
				})
				
			}else if(url == 'meReserve'){
				uni.navigateTo({
					url: `/pages/indexAccurate?id=${9}&name=${'备用金打造'}`
				})
			}else if(url == 'meAllProduct'){
				uni.navigateTo({
					url: `/pages/indexAccurate?id=${11}&name=${'各省产品汇总'}`
				})
			}else if(url == 'rejection'){
				uni.navigateTo({
					url: `/pages/indexAccurate?id=${8}&name=${'拒贷汇总'}`
				})
			}else if(url == 'risk'){
				uni.navigateTo({
					url: `/pages/indexAccurate?id=${8}&name=${'风险把控'}`
				})
			}else if(url == 'loan'){
				uni.navigateTo({
					url: `/pages/indexAccurate?id=${8}&name=${'贷款流程'}`
				})
			}else if(url == 'meSpread'){
				console.log(url)
				console.log(this.userInfo.type)
				if(this.userInfo.type == 'normal'){
					uni.showToast({
						title: '您不是会员，暂无此权限',
						icon: 'none',
						duration: 1000
					})
					setTimeout(e =>{
						uni.navigateTo({
							url: `/pages/meVIP`
						})
					},1600)
				}else{
					uni.navigateTo({
						url: `/pages/meSpread`
					})
				}
				
				// uni.navigateTo({
				// 	url: `/pages/indexAccurate?id=${11}&name=${'各省产品汇总'}`
				// })
			}else{
				uni.navigateTo({
					url: `/pages/${url}`
				})
			}
		},
		getUserInfo() {
			// 用户信息获取
			uni.showLoading({
			  title: '用户信息获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.statusCode == 200) {
						this.userInfo = res.data
						this.imageUrl = this.imgUrl+this.userInfo.avatar
					}
				}
			})
		}
	}
}
